import React, { useState, useEffect } from 'react';
import { Menu, Avatar, Typography, Space, Divider } from 'antd';
import { Link, useNavigate } from 'react-router-dom';
import {
  HomeOutlined,
  FileTextOutlined,
  PlusOutlined,
  BarChartOutlined,
  TeamOutlined,
  LockOutlined,
  UserOutlined,
  LogoutOutlined,
  AuditOutlined,
  ToolOutlined,
  PieChartOutlined,
  LineChartOutlined
} from '@ant-design/icons';
import { authService } from '../../services/authService';

const { Text } = Typography;

const Sidebar = ({ role: propRole, collapsed }) => {
  const [role, setRole] = useState(propRole || localStorage.getItem('userRole'));
  const [currentUser, setCurrentUser] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    // propsrolelocalStorage
    if (!propRole) {
      const storedRole = localStorage.getItem('userRole');
      if (storedRole) {
        setRole(storedRole);
      } else {
        // localStoragecurrentUser
        authService.getCurrentUser().then(user => {
          if (user && user.role) {
            setRole(user.role);
            localStorage.setItem('userRole', user.role);
          }
        }).catch(err => {
          console.error(':', err);
        });
      }
    } else if (propRole !== role) {
      // propsrole
      setRole(propRole);
    }
  }, [propRole]);

  // 
  useEffect(() => {
    const userStr = localStorage.getItem('currentUser');
    if (userStr) {
      try {
        const userData = JSON.parse(userStr);
        setCurrentUser(userData);
      } catch (err) {
        console.error(':', err);
      }
    } else {
      // 
      authService.getCurrentUser()
        .then(user => {
          if (user) {
            setCurrentUser(user);
          }
        })
        .catch(err => {
          console.error(':', err);
        });
    }
  }, []);

  // 
  const isAdmin = role === '' || role === '' || role === '';
  const isDepartmentManager = role === '';
  const isSupervisor = role === '';
  const isSafetyOrOperationAdmin = role === '' || role === '';
  
  // 
  const canReview = isSupervisor || isAdmin;
  
  // 
  const canManageImplementation = isAdmin;
  
  // 
  const canSubmitSuggestion = !isSafetyOrOperationAdmin;

  // 
  const canViewTeamInternalReport = isSupervisor;

  console.log('Sidebar - :', role);
  console.log('Sidebar - :', isAdmin);
  console.log('Sidebar - :', isDepartmentManager);
  console.log('Sidebar - :', canSubmitSuggestion);
  console.log('Sidebar - :', canViewTeamInternalReport);

  const handleLogout = () => {
    authService.logout();
    navigate('/login');
  };

  return (
    <>
      {/*  -  */}
      <div style={{ 
        padding: collapsed ? '16px 0' : '16px', 
        textAlign: 'center',
        overflow: 'hidden'
      }}>
        <Space direction="vertical" size="small" style={{ width: '100%' }}>
          <Avatar 
            size={collapsed ? 36 : 64} 
            icon={<UserOutlined />} 
            style={{ backgroundColor: '#1890ff' }} 
          />
          {!collapsed && (
            <>
              <Text strong style={{ fontSize: '16px' }}>
                {currentUser?.name || '...'}
              </Text>
              <Text type="secondary">
                {role || '...'}
              </Text>
              {currentUser?.team && (
                <Text type="secondary">
                  {currentUser.team}
                </Text>
              )}
            </>
          )}
        </Space>
      </div>
      {!collapsed && <Divider style={{ margin: '0 0 8px 0' }} />}
      
      <Menu
        mode="inline"
        defaultSelectedKeys={['1']}
        style={{ borderRight: 0 }}
        inlineCollapsed={collapsed}
      >
        <Menu.Item key="1" icon={<HomeOutlined />}>
          <Link to="/dashboard"></Link>
        </Menu.Item>
        
        <Menu.Item key="2" icon={<FileTextOutlined />}>
          <Link to="/suggestions/list"></Link>
        </Menu.Item>
        
        {canSubmitSuggestion && (
          <Menu.Item key="3" icon={<PlusOutlined />}>
            <Link to="/suggestions/new"></Link>
          </Menu.Item>
        )}

        {canReview && (
          <Menu.Item key="4" icon={<AuditOutlined />}>
            <Link to="/suggestions/review"></Link>
          </Menu.Item>
        )}

        {canManageImplementation && (
          <Menu.Item key="5" icon={<ToolOutlined />}>
            <Link to="/suggestions/implementation"></Link>
          </Menu.Item>
        )}

        {isAdmin && (
          <Menu.Item key="6" icon={<BarChartOutlined />}>
            <Link to="/reports/department-performance"></Link>
          </Menu.Item>
        )}

        {canViewTeamInternalReport && (
          <Menu.Item key="7" icon={<LineChartOutlined />}>
            <Link to="/reports/team-internal-report"></Link>
          </Menu.Item>
        )}

        {isDepartmentManager && (
          <Menu.Item key="8" icon={<TeamOutlined />}>
            <Link to="/users"></Link>
          </Menu.Item>
        )}

        <Menu.Item key="9" icon={<LockOutlined />}>
          <Link to="/change-password"></Link>
        </Menu.Item>
        
        <Menu.Item key="10" icon={<LogoutOutlined />} onClick={handleLogout}>
          
        </Menu.Item>
      </Menu>
    </>
  );
};

export default Sidebar; 