import axios from 'axios';
import { message } from 'antd';

// APIURL
const getBaseUrl = () => {
  // API URL
  if (process.env.REACT_APP_API_URL) {
    return process.env.REACT_APP_API_URL;
  }
  
  // 
  if (process.env.NODE_ENV === 'production') {
    // URL
    if (window.location.hostname === 'diaodu.eu.org' || window.location.hostname.includes('yourdomain.com')) {
      return '/api'; // 
    } else {
      // IPURL
      const serverPort = '5000'; // 
      return `http://${window.location.hostname}:${serverPort}/api`;
    }
  } else {
    // localhost
    return 'http://localhost:5000/api';
  }
};

//  axios 
const api = axios.create({
  baseURL: getBaseUrl(),
  timeout: 30000, // 30
  headers: {
    'Content-Type': 'application/json'
  }
});

// APIURL
console.log('API URL:', getBaseUrl());

//  token
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      // token
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    // FormDataContent-Type
    if (config.data instanceof FormData) {
      // axiosFormDataContent-Type
      config.headers['Content-Type'] = undefined;
    }
    
    console.log(':', {
      url: config.url,
      method: config.method,
      baseURL: config.baseURL,
      token: token ? '' : ''
    });
    return config;
  },
  (error) => {
    console.error(':', error);
    return Promise.reject(error);
  }
);

// 
api.interceptors.response.use(
  (response) => {
    // response.data
    return response.data;
  },
  (error) => {
    console.error('API:', error);
    
    let errorMsg = '';
    
    if (error.response) {
      // 
      console.error(':', {
        status: error.response.status,
        data: error.response.data
      });
      
      // 401
      if (error.response.status === 401) {
        const errorMessage = error.response.data?.message || error.response.data?.msg;
        if (errorMessage?.includes('') || errorMessage?.includes('')) {
          // token
          console.log('');
          localStorage.removeItem('token');
          localStorage.removeItem('userId');
          localStorage.removeItem('currentUser');
          window.location.href = '/login';
        }
        errorMsg = '';
      } 
      // 400
      else if (error.response.status === 400) {
        const errorData = error.response.data;
        if (errorData?.errors && Array.isArray(errorData.errors)) {
          // 
          errorMsg = errorData.errors[0]?.msg || errorData.errors[0]?.message || '';
        } else {
          // 
          errorMsg = errorData?.message || errorData?.msg || '';
        }
      }
      // 500
      else if (error.response.status >= 500) {
        errorMsg = '';
      }
      // 
      else {
        errorMsg = error.response.data?.message || 
                  error.response.data?.msg || 
                  ': ' + error.response.status;
      }
    } 
    else if (error.request) {
      // 
      console.error(':', error.request);
      errorMsg = '';
    } 
    else {
      // 
      console.error(':', error.message);
      errorMsg = ': ' + error.message;
    }
    
    // 
    if (error.config?.showErrorMessage !== false) {
      message.error(errorMsg);
    }
    
    return Promise.reject({
      ...error,
      userMessage: errorMsg
    });
  }
);

// API
api.checkHealth = async () => {
  try {
    const response = await api.get('/health');
    return {
      success: true,
      status: response.status,
      message: response.message,
      time: response.time
    };
  } catch (error) {
    return {
      success: false,
      message: error.userMessage || ''
    };
  }
};

export default api; 