import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { message, Spin } from 'antd';
import { authService } from '../services/authService';

/**
 * 
 * 
 */
const DepartmentManagerRoute = ({ children }) => {
  const [loading, setLoading] = useState(true);
  const [hasAccess, setHasAccess] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const checkAccess = async () => {
      try {
        setLoading(true);
        setError(null);
        
        // 
        const hasDepartmentManagerAccess = await authService.isDepartmentManager();
        setHasAccess(hasDepartmentManagerAccess);
        
        if (!hasDepartmentManagerAccess) {
          setError('');
        }
      } catch (error) {
        console.error(':', error);
        setHasAccess(false);
        setError(error.message || '');
      } finally {
        setLoading(false);
      }
    };

    checkAccess();
  }, []);

  if (loading) {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <Spin size="large" tip="..." />
      </div>
    );
  }

  if (error || !hasAccess) {
    console.log('DepartmentManagerRoute - ');
    message.error('');
    return <Navigate to="/" replace />;
  }

  console.log('DepartmentManagerRoute - ');
  return children;
};

export default DepartmentManagerRoute; 