import api from './api';

const saveUserToLocalStorage = (userData) => {
  try {
    if (!userData) {
      console.error(': ');
      return;
    }
    
    console.log('localStorage:', {
      id: userData._id || userData.id,
      name: userData.name,
      role: userData.role,
      team: userData.team
    });
    
    // ID
    const userToSave = { ...userData };
    if (userData._id && !userData.id) {
      userToSave.id = userData._id;
    }
    
    localStorage.setItem('currentUser', JSON.stringify(userToSave));
    
    // 
    if (userData.role) {
      localStorage.setItem('userRole', userData.role);
    }
    
    console.log('localStorage');
  } catch (error) {
    console.error(':', error);
  }
};

export const authService = {
  login: async (username, password) => {
    try {
      console.log('...');
      const response = await api.post('/auth/login', { username, password });
      console.log(':', response);
      
      // tokenuser
      const token = response.token;
      const user = response.user;
      
      if (!token || !user) {
        console.error(':', response);
        throw new Error('');
      }
      
      // token
      localStorage.setItem('token', token);
      localStorage.setItem('userId', user._id || user.id);
      
      // 
      saveUserToLocalStorage(user);
      
      return { token, user };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  logout: () => {
    console.log('');
    localStorage.removeItem('token');
    localStorage.removeItem('userId');
    localStorage.removeItem('currentUser');
    localStorage.removeItem('userRole');
  },

  getCurrentUser: async () => {
    try {
      // localStorage
      const userJson = localStorage.getItem('currentUser');
      if (userJson) {
        console.log('getCurrentUser: localStorage');
        return JSON.parse(userJson);
      }
      
      console.log('getCurrentUser: localStorage');
      
      // token
      const token = localStorage.getItem('token');
      if (!token) {
        console.log('getCurrentUser: token');
        return null;
      }
      
      console.log('getCurrentUser: token');
      
      // API
      const response = await api.get('/auth/me');
      
      // 
      console.log(':', response);
      
      // API
      // API
      const userData = response;
      
      if (!userData || (!userData._id && !userData.id)) {
        console.error('getCurrentUser: ', response);
        return null;
      }
      
      // localStorage
      saveUserToLocalStorage(userData);
      
      return userData;
    } catch (error) {
      console.error(':', error);
      return null;
    }
  },

  isAuthenticated: () => {
    const token = localStorage.getItem('token');
    console.log(':', !!token);
    return !!token;
  },
  
  // 
  isDepartmentManager: async () => {
    try {
      // localStorage
      const roleFromStorage = localStorage.getItem('userRole');
      if (roleFromStorage === '') {
        return true;
      }
      
      // API
      const user = await authService.getCurrentUser();
      if (user && user.role === '') {
        return true;
      }
      
      return false;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  },
  
  // 
  isAdmin: async () => {
    try {
      // localStorage
      const roleFromStorage = localStorage.getItem('userRole');
      if (roleFromStorage === '' || 
          roleFromStorage === '' || 
          roleFromStorage === '') {
        return true;
      }
      
      // API
      const user = await authService.getCurrentUser();
      if (user && (
        user.role === '' || 
        user.role === '' || 
        user.role === ''
      )) {
        return true;
      }
      
      return false;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  },
  
  // 
  isShiftSupervisor: async () => {
    try {
      // localStorage
      const roleFromStorage = localStorage.getItem('userRole');
      if (roleFromStorage === '') {
        return true;
      }
      
      // API
      const user = await authService.getCurrentUser();
      if (user && user.role === '') {
        return true;
      }
      
      return false;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  },
  
  // 
  hasReviewPermission: async () => {
    try {
      // localStorage
      const roleFromStorage = localStorage.getItem('userRole');
      if (roleFromStorage === '' || 
          roleFromStorage === '' || 
          roleFromStorage === '' ||
          roleFromStorage === '') {
        console.log('hasReviewPermission: (localStorage)', roleFromStorage);
        return true;
      }
      
      // API
      const user = await authService.getCurrentUser();
      console.log('hasReviewPermission: API', user);
      
      if (user && (
        user.role === '' || 
        user.role === '' || 
        user.role === '' ||
        user.role === ''
      )) {
        console.log('hasReviewPermission: (API)', user.role);
        return true;
      }
      
      console.log('hasReviewPermission: ');
      return false;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  },
  
  // 
  getUserRole: () => {
    // localStorage
    const roleFromStorage = localStorage.getItem('userRole');
    if (roleFromStorage) {
      return roleFromStorage;
    }
    
    // currentUser
    try {
      const userStr = localStorage.getItem('currentUser');
      if (userStr) {
        const user = JSON.parse(userStr);
        if (user && user.role) {
          return user.role;
        }
      }
    } catch (error) {
      console.error(':', error);
    }
    
    return null;
  }
}; 